import Vuex from 'vuex'
import Vue from 'vue'

Vue.use(Vuex)

var store = new Vuex.Store({
  state: {
    currentVersion: 'v1.0.7',
    lastVersion: '',
    helloTitle: '',
    helloMessage: '',
    applications: []
  },
  getters: {
    applications: state => {
      return state.applications.filter(app => app.active)
    }
  }
})

export default store



